<template>
	<view class="form-box">
		<!-- 账号 -->
		<view class="form-item-box">
			<input style="width: 100%;" type="text" :placeholder="accountPlaceHolder" placeholder-style="color:#D4D9E7" v-model="username" />
		</view>
		<!-- 密码 -->
		<view class="form-item-box password-input">
			<view>
				<!-- 显示密码 -->
				<input 
				type="text" 
				v-if="isShowPassword" 
				:placeholder="passwordPlaceHolder"
				placeholder-style="color:#D4D9E7" 
				v-model="password"
				maxlength="12" />
				<!-- 不显示密码 -->
				<input 
				type="text" 
				password 
				v-else 
				:placeholder="passwordPlaceHolder" 
				placeholder-style="color:#D4D9E7" 
				v-model="password" 
				maxlength="12" />
			</view>
			<view class="see-icon-box">
				<image 
				:src="isShowPassword?'../../static/img/eye-o.png':'../../static/img/eye.png'" 
				@click="showPassword"
				 mode="widthFix"></image>
			</view>
		</view>
		<!-- 间距 -->
		<view class="space-80"></view>
		<!-- 按钮 -->
		<u-button type="primary" shape="circle" :ripple="true" @click="handleSubmit">{{ i18n.signIn }}</u-button>
		<!-- 监听按钮事件 -->
		<keyboard-listener @keydown="handleKeydown"></keyboard-listener>
	</view>
</template>

<script>
	import keyboardListener from '@/components/keyboard-listener/keyboard-listener.vue'
	export default {
		components: {
			keyboardListener
		},
		data() {
			return {
				// 是否显示密码
				isShowPassword: false,
				// 账号
				username: '',
				// 密码
				password: ''
			}
		},
		methods: {
			// 按下enter键直接登录
			handleKeydown(event) {
				if (event.keyCode === 13) {
					this.handleSubmit()
				}
			},
			// 点击眼睛切换密码显示
			showPassword(){
				this.isShowPassword = !this.isShowPassword
			},
			// 点击登录按钮
			async handleSubmit() {
				// 验证是否都不为空
				if (this.username !== '' && this.password !== '') {
					if (!/^.{6,12}$/.test(this.password)) {
						await uni.showToast({
							title: this.$t('login.verificationTips1'),
							icon: 'none'
						})
						return
					}
					// 发送事件给父组件
					this.$emit('passwordSubmit', {
						username: this.username.trim(),
						password: this.password.trim()
					})
				} else {
					uni.showToast({
						title: this.$t('login.verificationTips2'),
						icon: 'none'
					})
				}	
			}
		},
		computed: {
			// 国际化
			i18n() {
				return this.$t('login')
			},
			accountPlaceHolder() {
				return this.$t('login.accountPlaceHolder')
			},
			passwordPlaceHolder() {
				return this.$t('login.passwordPlaceHolder')
			}
		}
	}
</script>

<style lang="scss" scoped>
	.form-box {
		margin-top: 160rpx;
		padding: 0 80rpx;
		.form-item-box {
			height: 94rpx;
			background-color: #f9fcff;
			border-radius: 20rpx;
			display: flex;
			align-items: center;
			padding: 0 20rpx;
			input {
				height: 94rpx;
			}
			.see-icon-box{
				width: 34rpx;
				height: 23rpx;
				image{
					height: 100%;
				}
			}
		}
		.form-item-box:first-of-type {
			margin-bottom: 70rpx;
		}
		.password-input{
			justify-content: space-between;
			padding-right: 30rpx;
		}
	}
	.space-80{
		height: 80rpx;
	}
	.placeholder{
		color: red!important;
	}
</style>
